
/* CSS for Leftward Scrolling Text */
.running-text {
  overflow: hidden; /* Hide overflowing content */
  white-space: nowrap;
  width: 100%;
  margin-bottom: 30px;
}

.running-text h2 {
  display: inline-block; /* Make the text inline */
  animation: scrollLeft 10s linear infinite alternate; /* Adjust the animation duration as needed */
}

@keyframes scrollLeft {
  from {
    transform: translateX(100%); /* Start off-screen to the right */
  }

  to {
    transform: translateX(-100%); /* Scroll to off-screen to the left */
  }
}

